<template>
	<view>
		<scroll-view scroll-y="true" :style="'height:' + scrollHeight + 'px;'"
			style="width: 100%;background-color: white;" class="column" v-if="item">
			<view class="ordernum">{{i18n.shop.ordernum}}{{order.order_id}}</view>
			<view class="goodsallnum">{{i18n.shop.goodsnum}}:{{i18n.shop.amount}}{{order.total_num}}{{i18n.shop.jianshu}}
			</view>
			<view class="section2_list" v-for="(item,index) in cartInfo" :key="index">
				<view class="goodsname">{{item.product.use_name}}</view>
				<view class="goodsnum">x{{item.cart_num}}</view>
				<view class="goodsprice">{{item.product.price}}</view>
			</view>
			<view style="width: 100%;height: 1px;background-color: #e6e6e6;"></view>
			<!-- 用户信息 -->
			<view class="row" style="width: 100%;">
				<block v-if="item.is_anonymous==1">
					<image :src="defaultAvatar" mode="aspectFill" class="iconImage" @click.stop="doUserCenter(item)"></image>
				</block>
				<block v-else>
					<image :src="item.users.avatar" mode="aspectFill" class="iconImage" @click.stop="doUserCenter(item)"></image>
				</block>
				<view class="nameTimeView colum">
					<view v-if="item.is_anonymous==1" class="userNickeName">
						{{i18n.index.niming}}
					</view>
					<view v-else class="userNickeName">{{item.users.user_nickname}}</view>
					<view class="userNickeName" style="color: #646464;font-size: 12px;">{{item.addtime}}</view>
				</view>
				<view class="starbg">
					<starV size=30 @change="onChange" disabled=true :value="item.overall_star"></starV>
				</view>
			</view>
			<!-- 评论内容 -->
			<view class="textContent">{{item.content}}</view>
			<!-- 评论图片 -->
			<view class="middle-Content">
				<view v-for="(url,index_i) in item.pics" :key="index_i" class="picBackView">
					<image :src="url" mode="aspectFill" class="pic-image" @click.stop="showBigImage(index_i,item.pics)">
					</image>
				</view>
			</view>
			<!-- 底部赞个数 服务类型订单没有点赞数-->
			<view  class="bottomView row">
				<view v-show="firstPlatformID !=3 " style="color: #6F6F6F;font-size: 14px;">{{i18n.index.zannum}} {{item.like}}</view>
				<view v-show="item.is_reply==0" class="reply" @click="reply(item)">{{i18n.index.reply}}</view>
			</view>
			<view v-show="item.is_reply==1" class="textContent"
				style="font-size: 12px;background-color: #F5F6F7;border-radius: 4px;">
				<view style="color: #00C1DE;margin-left: 8px;margin-top: 8px;">
					{{i18n.index.shopreply}}
				</view>
				<view style="color: #383838;margin-left: 8px;margin-top: 8px;padding-bottom: 8px;">
					{{item.merchant_reply_content}}
				</view>
			</view>
			<!-- 回复弹出窗 -->
			<view v-show="showPop" class="replypop">
				<view class="bg">
					<view class="pop-txt">{{i18n.index.reply}}</view>
					<input class="input" type="text" value="" @input="inputreply" />
					<view class="publish" @click="puhlish()">{{i18n.index.publish}}</view>
				</view>
			</view>
		</scroll-view>
	</view>
</template>

<script>
	import pingjia from '@/components/pingjia/pingjia.vue'
	import starV from '@/components/banxing/banStar.vue'
	export default {
		components: {
			starV,
			pingjia
		},
		data() {
			return {
				scrollHeight: 0,
				commentID: '',
				item:null,
				orderNumID: '',
				goodsNum: '',
				cartInfo: [],
				showPop:false,
				replyTxt:'',
				replyID:'',
				order:{},
				firstPlatformID:'',
				defaultAvatar:''
			} 
		},
		computed: {
			i18n() {
				return this.$t('index')
			}
		},
		onReady() {
			const res = uni.getSystemInfoSync();
			this.scrollHeight = res.windowHeight
		},
		onLoad(option) {
			uni.setNavigationBarTitle({
				title: this.i18n.shop.pinglun
			})
			this.defaultAvatar = getApp().globalData.weburl + 'default.png'
			var shopinfo = uni.getStorageSync('shopinfo')
			// console.log(shopinfo)
			this.firstPlatformID = shopinfo.top_type_id
			
			var dic = JSON.parse(option.param)
			this.commentID = dic.id
			// this.orderNumID = dic.order.order_id
			// this.goodsNum = dic.order.total_num
			// console.log(option)
			this.getData()
			var that = this
			uni.$on('refreshReply', (param) => {
				that.getData()
			})
		},
		onShow() {
			this.getData()
		},
		onUnload() {
			uni.$off('refreshReply')
		},
		methods: {
			getData() {
				var that = this
				var param = {
					'id': this.commentID
				}
				this.http.sendRequest('MerchantStoreOrderEvaluate.Detail', param).then(function(data) {
					console.log(data)
					if (data.code == 0) {
						that.item = data.info[0]
						console.log(that.item)
						that.cartInfo = that.item.cart_info
						that.order = that.item.order
						console.log(that.cartInfo)
					}
				})
			},
			showBigImage(index,pics){
				uni.previewImage({
					current: index,
					urls:pics,
				})
				
			},
			inputreply(e){
				this.replyTxt = e.detail.value
			},
			onChange(){
				
			},
			reply(item){
				this.showPop = true
				this.replyID = item.id
				
			},
			puhlish(){
				this.showPop = false
				if(this.replyTxt.length>0){
					var param = {'id':this.replyID,
								 'merchant_reply_content':this.replyTxt}
					var that = this
					this.http.sendRequest('MerchantStoreOrderEvaluate.ReplySave',param).then(function(data) {
						if(data.code == 0){
							that.showPop = false
							that.getData()
						}
					})
				}
				
			},
		}
	}
</script>

